/* Home.css */
.container {
   height: 100%;
   display: flex;
   justify-content: center;
}

.loader {
   width: 100%; 
   height: 100vh;
   display: flex;
   justify-content: center;
   align-items: center;
   font-weight: 300;
}

.movies {
   display: grid;
   grid-template-columns: repeat(2, minmax(400px, 1fr));
   /* 2열로 최소크기 400px 1:1 비율로 */
   grid-gap: 100px;
   /* grid 사이의 갭은 100px */
   padding: 50px;
   width: 80%;
   padding-top: 70px;
}

/* 0 ~ 767px */
@media screen and (max-width: 767px) {
   .movies {
      grid-template-columns: 1fr;
      width: 100%;
   }
}